<template>
  <div class="container">
    <div class="header">
      <div class="select" style="margin-right: 20px;">
        <span class="span">页面（{{this.options.length}} 个）</span>
        <el-select v-model="value8" filterable placeholder="请选择" @change="changeOptions">
          <el-option
            v-for="item in options"
            :key="item.value"
            :label="item.label"
            :value="item.value">
          </el-option>
        </el-select>
      </div>
      <div class="select" style="margin-left: 20px;">
        <span class="span">组件（{{this.options2.length}} 个）</span>
        <el-select v-model="value9" filterable placeholder="请选择" @change="changeOptions">
          <el-option
            v-for="item in options2"
            :key="item.value"
            :label="item.label"
            :value="item.value">
          </el-option>
        </el-select>
      </div>
      <div class="select" style="margin-left: 40px;">
        <span class="span">被引用关系</span>
        <el-select v-model="value10" filterable placeholder="请选择" @change="changeOptions1">
          <el-option
            v-for="item in options3"
            :key="item.value"
            :label="item.label"
            :value="item.value">
          </el-option>
        </el-select>
      </div>
      <div class="btn" style="margin-left: 20px;">
        <el-button @click="reset">概览 </el-button>
      </div>
    </div>
    <div id="main"></div>
  </div>
</template>

<script>
import axios from 'axios';

  export default {
    data() {
      return {
        data: [],
        echarts: {},
        otherEcharts: {},
        options:[],
        options2:[],
        options3: [],
        currentData: [],
        value8:'',
        value9: '',
        value10: ''
      }
    },
    computed:{
      option() {
        return {
          tooltip: {
            trigger: 'item',
            triggerOn: 'mousemove'
          },
          series: [
            {
              type: 'tree',
              data: [{
                name: '发现H5组件引用与被引用关系',
                children: this.currentData
              }],
              initialTreeDepth: -1,
              top: '1%',
              left: 200,
              right: 150,
              bottom: 1,
              symbolSize: 7,
              label: {
                  normal: {
                      position: 'left',
                      verticalAlign: 'middle',
                      align: 'right',
                      fontSize: 11
                  }
              },
              leaves: {
                  label: {
                      normal: {
                          position: 'right',
                          verticalAlign: 'middle',
                          align: 'left'
                      }
                  }
              },
              expandAndCollapse: true,
              animationDuration: 550,
              animationDurationUpdate: 750
            }
          ]
        }
      }
    },
    mounted() {
      var myChart = echarts.init(document.getElementById('main'));
      this.echarts = myChart
        // 指定图表的配置项和数据
      axios.get('/getData').then(res => {
        let pageOptions = res.data.pages.map(i => {
          return {
            value: i.name,
            lable: i.name
          }
        })
        this.data = res.data
        this.options = pageOptions
        this.options2 = res.data.components.map(i => {
          return {
            value: i.name,
            lable: i.name
          }
        })
        this.options3 = res.data.reverseTree.map(i => {
          return {value: i.name, lable: i.name}
        })
        this.currentData = res.data.pages
        this.echarts.setOption(this.option);
      })
    },
    methods: {
      changeOptions(val) {
        const selectedItem = this.data.all.filter(i => {
          return val === i.name
        })
        this.currentData = selectedItem;
        this.option.series[0].right = 400
        this.echarts.resize({
          width: 1500,
          height: 500
        })
        this.echarts.setOption(this.option)
      },
      changeOptions1(val) {
        const selectedItem = this.data.reverseTree.filter(i => {
          return val === i.name
        })
        this.currentData = selectedItem;
        this.option.series[0].right = 400
        this.echarts.resize({
          width: 1500,
          height: 500
        })
        this.echarts.setOption(this.option)
      },
      reset() {
        this.echarts.resize({
          width: 1800,
          height: 2000
        })
        this.currentData = this.data.pages
        this.echarts.setOption(this.option)
      }
    }
  }
</script>


<style>
  .header{
    width: 100vw;
    background: skyblue;
    display: flex;
    justify-content: center;
  }
  #main {
    width: 100vw;
    height: 2000px;
    margin-top: 30px;
    margin-bottom: 30px;
  }
  .span {
    font-weight: bold;
  }
</style>



